import React from "react";
import "./App.css";
import { Navigate, Route, Routes, useLocation } from "react-router-dom";
import { Direction } from "./type";
import HomePage from "./pages/home";
import TourDetailPage from "./pages/tour.detail";
import { AnimatePresence } from "framer-motion";
import SchedulePage from "./pages/schedule";
import PageTransition from "./pages/page.transition";
import ProfilePage from "./pages/profile";
import Schedule3Page from "./pages/schedule/schedule3";
import Schedule7Page from "./pages/schedule/schedule7";
import HotelCheckInPage from "./pages/schedule/schedule7/hotel.checkin";
import ContactPage from "./pages/contact";
import CityPage from "./pages/city";
import CityHotelPage from "./pages/city.hotel";

const App: React.FC = () => {
  const location = useLocation();
  const direction: Direction = location.pathname === "/" ? 1 : -1;

  return (
    <AnimatePresence mode="wait">
      <Routes location={location} key={location.pathname}>
        <Route
          path="/"
          element={
            <PageTransition direction={direction}>
              <HomePage />
            </PageTransition>
          }
        />
        <Route
          path="/tour-detail"
          element={
            <PageTransition direction={-1}>
              <TourDetailPage />
            </PageTransition>
          }
        />
        <Route
          path="/profile"
          element={
            <PageTransition direction={-1}>
              <ProfilePage />
            </PageTransition>
          }
        />
        <Route
          path="/schedule"
          element={
            <PageTransition direction={-1}>
              <SchedulePage />
            </PageTransition>
          }
        />
        <Route
          path="/schedule/3"
          element={
            <PageTransition direction={-1}>
              <Schedule3Page />
            </PageTransition>
          }
        />
        <Route
          path="/schedule/7"
          element={
            <PageTransition direction={-1}>
              <Schedule7Page />
            </PageTransition>
          }
        />
        <Route
          path="/schedule/7/hotel-check-in"
          element={
            <PageTransition direction={-1}>
              <HotelCheckInPage />
            </PageTransition>
          }
        />
        <Route
          path="/contact"
          element={
            <PageTransition direction={-1}>
              <ContactPage />
            </PageTransition>
          }
        />
        <Route
          path="/contact/:city"
          element={
            <PageTransition direction={-1}>
              <CityPage />
            </PageTransition>
          }
        />
        <Route
          path="/contact/:city/hotel"
          element={
            <PageTransition direction={-1}>
              <CityHotelPage />
            </PageTransition>
          }
        />
      </Routes>
    </AnimatePresence>
  );
};
export default App;
