import React from "react";
import HeaderComponent from "../../../components/header";
import { Link, useNavigate } from "react-router-dom";
import ComplexButtonComponent from "../../../components/complex.button";
import NavButtonComponent from "../../../components/nav.button";
export const Schedule7Page: React.FC = () => {
  const navigate = useNavigate();
  const data = [
    {
      type: true,
      left_title: ["8am", "(or earlier)"],
      right_title: "Hotel checkout",
      left_className: "bg-darkyellow border-secondary",
      right_className: "bg-darkyellow border-secondary",
      path: "/schedule/7",
    },
    {
      type: true,
      left_title: ["8:30am"],
      right_title: "Shuttle to airport",
      left_className: "bg-darkyellow border-secondary",
      right_className: "bg-darkyellow border-secondary",
      path: "/schedule/7",
    },
    {
      type: true,
      left_title: ["11:05am"],
      right_title: "Flight LV to NYC",
      left_className: "bg-darkyellow border-secondary",
      right_className: "bg-secondary border-darkyellow",
      path: "/schedule/7",
    },
    {
      type: false,
      title: "Train to hotel",
      className:
        "bg-secondary border-darkyellow text-[18.25px] font-light mb-1",
      path: "/schedule/7",
    },
    {
      type: false,
      title: "Hotel check-in",
      className:
        "bg-secondary border-darkyellow text-[18.25px] font-light mb-1",
      path: "/schedule/7/hotel-check-in",
    },
    {
      type: true,
      left_title: ["7pm-", "9pm"],
      right_title: "Night bus (optional)",
      left_className: "bg-secondary border-darkyellow",
      right_className: "bg-secondary border-darkyellow",
      path: "/schedule/7",
    },
  ];
  return (
    <>
      <HeaderComponent>
        <Link to="/schedule">
          <img
            src={`${process.env.PUBLIC_URL}/assets/images/left_yellow.png`}
          />
        </Link>
        <div className="text-[24.08px] font-bold text-center leading-none">
          <div className="flex items-center">
            <span className="text-primary">LA</span>
            <img
              src={`${process.env.PUBLIC_URL}/assets/images/arrow_green.png`}
              className="w-fit h-fit"
            />
            <span className="text-darkyellow">Vegas</span>
            <img
              src={`${process.env.PUBLIC_URL}/assets/images/arrow_yellow.png`}
              className="w-fit h-fit"
            />
            <span className="text-secondary">NYC</span>
          </div>
          <div className="text-secondary">September 7</div>
        </div>
      </HeaderComponent>
      <div className="mx-3">
        {data.map((val) => {
          return val.type ? (
            <ComplexButtonComponent
              key={val.right_title}
              left_className={val.left_className || ""}
              left_title={val.left_title || [""]}
              right_title={val.right_title || ""}
              right_className={val.right_className || ""}
              onClick={() => navigate(val.path)}
            />
          ) : (
            <NavButtonComponent
              key={val.title}
              title={val.title || ""}
              className={val.className || ""}
              onClick={() => {
                navigate(val.path);
                console.log(val.path);
              }}
            />
          );
        })}
      </div>
    </>
  );
};
